.main-heading{
    text-align: center;
    margin: 9rem;
    display: flex;

}

.main-home-page{
    background: #0f0c29;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #24243e, #302b63, #0f0c29);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #24243e, #302b63, #0f0c29); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    
}
.btn-main{
    background-color: #6149e1;
    padding: 0 10px;
    min-height: 70px;
    line-height: 70px;
    font-size: 1.7rem;
}
.def-btn {
    min-width: 300px;
    /* min-height: 90px;
    display: -webkit-box;
    display: -ms-flexbox; */
    display: flex;
    -webkit-box-align: center;
    /* -ms-flex-align: center; */
    align-items: center;
    -webkit-box-pack: center;
    /* -ms-flex-pack: center; */
    justify-content: center;
    /* font-size: 32px; */
    font-family: "Neuron Angled", sans-serif;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
    border-radius: 45px;
    /* line-height: 80px; */
    color: #ffffff;
    /* -webkit-transition: background-color 0.3s ease-in-out; */
    transition: background-color 0.3s ease-in-out;
    /* padding-bottom: 6px; */
    margin: 1rem;
}

.main-btn{
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.first-heading{
    font-size: x-large;
    color: white;
    margin: 2rem;
    display: flex;
    justify-content: center;
    text-align: center;
}

.sec-heading{
    color: white;
    font-size: larger;
    margin: 2rem;
    display: flex;
    justify-content: center;
    text-align: center;
}

.main-logo{
    width: 100%;
  height: auto;
}