.community{
    text-align: center;
    padding: 3rem;
}

.plus-line::after{
    content: '';
    position: absolute;
    left: 11px;
    top: 44px;
    bottom: 0;
    width: 2px;
    background-color: whitesmoke;
    
}

.plus-line::before{
    content: '';
    position: absolute;
    top: -6px;
    left: 0;
    width: 24px;
    height: 24px;
    background: url("https://forsage.io/img/icons/plus_icon.png") no-repeat center;
    background-size: 100% 100%;
}

.default-title{
    letter-spacing: 1.5px;
    line-height: 100%;
    font-weight: bold;
    font-size: x-large;
    margin: 1rem;
}

.global-head{
    font-size: larger;
    margin: 1rem
}

.design{
    justify-content: center;
    display: flex;
    margin: 1px;
}