.section {
    position: relative;
    padding: 0;
    margin: 0;
}

.faq-content {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    height: 100%;
    position: relative;
    z-index: 5;
}

.section-content {
    position: relative;
    z-index: 5;
}

.faq-container {
    padding-bottom: 125px;
}

@media (max-height: 780px) and (min-width: 1175px){
.faq-title {
    padding-top: 40px;
}
}

.faq-title {
    padding: 30px 0 0;
    margin-bottom: 20px;
    text-align: center;
}

.accordion-wrap {
    width: 880px;
    margin: 0 auto 20px;
    padding-left: 38px;
    position: relative;
}
.accordion-wrap-2 {
    width: 880px;
    margin: 0 auto 20px;
    padding-left: 38px;
    position: relative;
}

.accordion-wraps {
    width: 880px;
    margin: 0 auto 20px;
    padding-left: 38px;
    position: relative;
}
.accordion-wrap::before {
    content: '';
    position: absolute;
    left: 0;
    top: 30px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #ffffff;
}

.accordion-wraps::before {
    content: '';
    position: absolute;
    left: 0;
    top: 30px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #ffffff;
}
.accordion-wrap-2::before {
    content: '';
    position: absolute;
    left: 0;
    top: 30px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #ffffff;
}
.accordion-wrap::after {
    content: '';
    position: absolute;
    left: 3px;
    width: 2px;
    top: 65px;
    bottom: -35px;
    background-color: #434246;
}

.revealator-slideright.revealator-within, .revealator-slideright.revealator-partially-above, .revealator-slideright.revealator-above {
    transform: translate(0, 0);
    opacity: 1;
}

.revealator-slideup, .revealator-slideleft, .revealator-slideright, .revealator-slidedown, .revealator-zoomin, .revealator-zoomout, .revealator-rotateleft, .revealator-rotateright {
    transition: all 600ms;
}

.accordion {
    background-color: #23203a;
    border-radius: 8px;
    cursor: pointer;
    width: 100%;
}

.accordion-heading {
    font-size: 1.25rem;
    text-transform: uppercase;
    padding: 22px 40px;
    font-family: "Neuron Angled", sans-serif;
    font-weight: bold;
    display: flex;
    -webkit-box-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    align-items: center;
    transition: color 0.3s ease-in-out;
}


.accordion-content {
    margin-top: 8px;
    line-height: 125%;
    padding: 0px 40px 32px;
    white-space: pre-wrap;
    white-space: normal;
    display: none;
}

.accordion-heading span {
    /* -webkit-transform: rotate(90deg); */
    /* transform: rotate(90deg); */
    /* -webkit-transform-origin: 50% 50%; */
    transform-origin: 50% 50%;
    /* -webkit-transition: 0.3s ease-in-out; */
    transition: 0.3s ease-in-out;
}

@media (max-width: 1175px){
.accordion-heading {
    padding: 20px 30px 12px 30px;
}
.accordion-wrap {
    padding-left: 0;
    width: calc(100% - 60px);
}
.accordion-wrap-2 {
    padding-left: 0;
    width: calc(100% - 60px);
}
.accordion-wraps {
    padding-left: 0;
    width: calc(100% - 60px);
}
.faq-container {
    width: 100%;
}
.faq-title {
    padding-top: 30px;
    margin: 0 auto 30px;
    width: calc(100% - 60px);
}
}

.revealator-slideleft.revealator-within, .revealator-slideleft.revealator-partially-above, .revealator-slideleft.revealator-above {
    transform: translate(0, 0);
    opacity: 1;
}


@media (max-width: 1500px){
.default-title h2 {
    font-size: 2.5rem;
}
}

.default-title h2 {
    letter-spacing: 1.5px;
    line-height: 100%;
    font-weight: bold;
    font-family: "Neuron Angled", sans-serif;
    color: #ffffff;
}

.default-title h2 span {
    color: #ffa243;
}

@media screen and (max-width: 992px) {
    .accordion-wrap::after {
        display: none;
    }
    .accordion-wrap::before {
        display: none;
    }
    .accordion-wraps::after {
        display: none;
    }
    .accordion-wraps::after {
        display: none;
    }
  }