.left-card {
  -webkit-box-shadow: 0 8px 6px -6px black;
  -moz-box-shadow: 0 8px 6px -6px black;
  box-shadow: 0 8px 6px -6px black;
  color: white;
  text-align: center;
  padding: 1rem;
  background-color: #13274f;
}

.box-data {
  color: white;
  padding: 1rem;
}

.box-card {
  border-radius: 8px;
  border: 1px solid gray;
  /* padding: 1rem; */
  border-top-right-radius: 6px;
  border-top-left-radius: 6px;
}

.box-header {
  background: gray;
  text-align: center;
}

.dot {
  height: 25px;
  width: 25px;
  background-color: blue;
  border-radius: 50%;
  display: inline-block;
  margin: 1px;
}

.dot2 {
  height: 25px;
  width: 25px;
  background-color: gray;
  border-radius: 50%;
  display: inline-block;
  margin: 1px;
}

.dot_desc {
  width: 65px;
  height: 30px;
  z-index: 999999;
  display: none;
  position: absolute;
  padding: 5px;
  font-size: 12.5px;
  color: white;
  background: #f3f3f3;
  text-align: right;
}

.orange-text {
  color: orange;
}

.buy__dot:before {
  content: '';
  height: 25px;
  width: 1px;
  position: absolute;
  bottom: 72%;
  left: 41px;
  border-left: 1px dashed #6d678f;
  transform: rotate(30deg);
}

.buy__dot2:before {
  content: '';
  height: 25px;
  width: 1px;
  position: absolute;
  bottom: 75%;
  left: 60px;
  border-left: 1px dashed #6d678f;
}

.buy__dot3:before {
  content: '';
  height: 25px;
  width: 1px;
  position: absolute;
  bottom: 73%;
  left: 77px;
  border-left: 1px dashed #6d678f;
  transform: rotate(-30deg);
}

.icon-span {
    margin-left: 1rem;
}
