.main-footer {
  /* background: #0f0c29;
    background: -webkit-linear-gradient(to right, #24243e, #302b63, #0f0c29);
    background: linear-gradient(to right, #24243e, #302b63, #0f0c29); */
  background-color: #23203a;
  clip-path: polygon(
    48% 48%,
    64% 20%,
    79% 43%,
    100% 0,
    100% 100%,
    0 100%,
    0 0,
    19% 47%,
    30% 18%
  );
  height: 20rem;
  display: flex;
  justify-content: center;
}

.footer-color {
  background: #0f0c29;
  background: -webkit-linear-gradient(to right, #24243e, #302b63, #0f0c29);
  background: linear-gradient(to right, #24243e, #302b63, #0f0c29);
}

.footer-inside-div {
  text-align: center;
  width: fit-content;
  color: white;
  position: absolute;
  bottom: 0;
  padding: 2rem;
}

.address-link:hover{
  cursor: pointer;
  color: crimson;
}